<h1 id="简介" tabindex="-1"><a class="header-anchor" href="#简介" aria-hidden="true">#</a> 简介</h1>
<p>作为一名前端开发，「表单开发」是我们的家常便饭，一般我们需要做以下重复性工作：</p>
<ul>
<li>编写模板</li>
<li>编写校验规则</li>
<li>获取数据，提交表单</li>
</ul>
<p>综上，我们希望能减少重复性工作，通过配置自动生成表单模板。<br>
于是便有了<a href="http://vueschemaform.aegis-info.com/doc/jieshao" target="_blank" rel="noopener noreferrer">VSchemaForm<OutboundLink/></a>的Vue表单解决方案。</p>
<p>此文档主要是具体使用例子。</p>
